import "dragdroptouch";
import 'font-awesome/css/font-awesome.css';
import './console.css';
import html from "./console.html";

import term from "../tty/tty";
import vio from "../vio/vio";
import dazzler from "../dazzler/dazzler";
import * as cyc from "../cyc/cyc";
import * as sys from "../sys/sys";
import * as cpa from "../cpa/cpa";
import * as cfg from "../cfg/cfg";
import {lib, dsk} from "../lib/diskmanager";

import * as Window from "./window";
import * as Modal from "./modal";

window.Window = Window;
window.Modal = Modal;
window.term = term;
window.vio = vio;
window.dzlr = dazzler;
window.cyc = cyc;
window.sys = sys;
window.dsk = dsk;
window.lib = lib;
window.cfg = cfg;
window.cpa = cpa;

// Stubbing out the drag-n-drop for uploading new files to TTY: LPT: SYS:
window.fileDragOverDrop = () => {};
window.fileDragOver = () => {};
window.fileDragEnd = () => {};
window.allowDriveDrop = () => {};

/**
 * TODO:
 * - have each module inject its own icon
 * - have modal:: inject its own html
 * - get the things in the list above out of global scope
 */

window.onload = function () {

    body.innerHTML = html;
    cpa.load();
    cfg.load();
    vio.load();
    dazzler.load();
    cyc.load();
    term.load();
    sys.load();
    lib.load();
    
    Window.init(TTY);
    Window.init(CRT);
    Window.init(DZLR);
    Window.init(CYC);
    Window.init(LIB);
    Window.init(SYS);
    Window.init(CPA);
    Window.init(CFG);
    
    term.connect();
    vio.connect();
    dazzler.connect();
    // cyc.connect();
    cpa.connect();
    lib.get();
    dsk.get();

    setTimeout(() => {
        let resize = new Event('resize');
        window.dispatchEvent(resize);
        body.classList.remove('hide');
    }, 50);
};

window.lpt = { 
    window: null,
    open: function () {
        if (this.window && !this.window.closed) {
            this.window.focus();
        } else {
            this.window = window.open("/printer");
        }
    }
}