import "./modal.css";

export {
    // init as init,
    showModal as show,
    closeModal as close,
    confirmModal as confirm
}

function confirmModal(msg, okCb, okLabel = 'OK', cancelCb) {

    let okButton = document.createElement('div');
    
    showModal(msg, null, cancelCb);
    okButton.innerHTML = okLabel;
    okButton.classList.add('modalBtn');
    okButton.addEventListener('click', okCb)
    modalFoot.appendChild(okButton);
}

let model;
/**
 * TODO: 
 *  - add a function to clear or update the body
 *  - inject the modal HTML into the DOM
 *  - accept both <string> and <DOMElement> as args for head, body
 */

function init() {

    modal = document.createElement('div');
    elmt.id = 'modal';

    body.appendChild(elmt);
}

let cb;

function showModal(head, foot, callback) {
    modalFoot.innerHTML = foot || "";
    modal.classList.add('modal-visible');
    modalHead.innerHTML = head || "";
    document.addEventListener('keydown', keyModal);
    cb = callback || null;
}
function closeModal() {
    modal.classList.remove('modal-visible');
    document.removeEventListener('keydown', keyModal);
    modalHead.innerHTML = "";
    modalBody.innerHTML = "";
    modalFoot.innerHTML = "";
    if (cb) { 
        cb();
        cb = null;
    }
}
function keyModal(ev) {
    if (ev.key == 'Escape') {
        closeModal();
        ev.preventDefault();
    }
}

