import 'xterm/lib/xterm.css';
import 'xterm/lib/addons/fullscreen/fullscreen.css'
import { Terminal } from 'xterm';
import * as fullscreen from 'xterm/lib/addons/fullscreen/fullscreen';
import * as attach from 'xterm/lib/addons/attach/attach';

import html from "./tty-window.html";

Terminal.applyAddon(fullscreen);
Terminal.applyAddon(attach);

let term = new Terminal({ fontFamily: "Menlo, Courier, monospace", fontSize: 15 });

term.load = function () {

  let elmt = document.createElement('div');
  
  elmt.id = 'TTY'
  elmt.innerHTML = html;

  windows.appendChild(elmt);

  elmt.addEventListener('click', (ev) => {
      term.focus();
      ev.preventDefault();
  });

  termFullscreen.addEventListener('click', () => {
      doToggleFullScreen();
  });

  term.open(termContainer)
}

function triggerEvent(el, type, key, mods) {
    let e = document.createEvent('HTMLEvents');
    e.keyCode = key.charCodeAt();
    e.ctrlKey = mods.ctrl || false;
    e.altKey = mods.alt || false;
    e.metalKey = mods.meta || false;
    e.shiftlKey = mods.shift || false;
    e.initEvent(type, false, true);
    el.dispatchEvent(e);
}

term.attachCustomKeyEventHandler(function (e) {
    let key = ('['
      + (e.ctrlKey ? 'C' : '')
      + (e.altKey ? 'A' : '')
      + (e.metaKey ? 'M' : '')
      + (e.shiftKey ? 'S' : '')
      + ']#' + e.key);

    function isLetter(str) {
      return str && str.length === 1 && str.match(/[a-z]/i); //todo: do wee need to support '@'
    }

    // console.log(e);

    if (e.altKey && e.code == "Escape") {
      if (e.type == "keydown") doToggleFullScreen();
      return false;;
    }

    if (keyStop[key]) return false;

    if (enableCtrl && e.type != 'keydown') return false;

    if (enableKeys && keyMap[key]) {
      triggerEvent(e.srcElement, 'keydown', keyMap[key].key, keyMap[key].mods);
      return false;
    }

    if (enableCtrl && isLetter(e.key)) {
      triggerEvent(e.srcElement, 'keydown', e.key.toUpperCase(), { ctrl: true });
      return false;
    }
  });

term.enableKeyMods = function () {
    enableKeys = !enableKeys;

    if (enableKeys) {
      keyMods.setAttribute('style', 'color: #fff;');
    } else {
      keyMods.setAttribute('style', 'color: #999;');
    }
    term.focus();
}

term.enableKeyCtrl = function () {
    enableCtrl = !enableCtrl;

    if (enableCtrl) {
      keyCtrl.setAttribute('style', 'color: #ff0;');
    } else {
      keyCtrl.setAttribute('style', 'color: #999;');
    }
    term.focus();
}

  let enableKeys = true;
  let enableCtrl = false;

  let keyStop = {
    "[C]#Tab": true,
  }

  let keyMap = {
    "[]#ArrowUp": { key: 'E', mods: { ctrl: true } },
    "[]#ArrowDown": { key: 'X', mods: { ctrl: true } },
    "[]#ArrowLeft": { key: 'S', mods: { ctrl: true } },
    "[]#ArrowRight": { key: 'D', mods: { ctrl: true } },
    "[A]#ArrowUp": { key: 'W', mods: { ctrl: true } },
    "[A]#ArrowDown": { key: 'Z', mods: { ctrl: true } },
    "[A]#ArrowLeft": { key: 'A', mods: { ctrl: true } },
    "[A]#ArrowRight": { key: 'F', mods: { ctrl: true } },
    "[AS]#ArrowUp": { key: 'R', mods: { ctrl: true } },
    "[AS]#ArrowDown": { key: 'C', mods: { ctrl: true } },
    "[]#Backspace": { key: 'H', mods: { ctrl: true } },
    "[]#Delete": { key: 'G', mods: { ctrl: true } }
  }

  let isFullscreen = false;

  function doToggleFullScreen() {

    if (isFullscreen) {
      term.setOption('fontSize', 15);
      term.toggleFullScreen(false);
      isFullscreen = false;
    } else {
      term.toggleFullScreen(true);
      isFullscreen = true;

      // let w = term.element.clientWidth;
      // let f = Math.floor(w / 80 / 0.6);
      let h = term.element.clientHeight;
      let f = (h / 25 * 0.8333);

      term.setOption('fontSize', f);
    }
  }

term.connect = function () {
    if (term.__socket) {
        Modal.confirm("Disconnect TTY from IMSAI 8080?", () => {
            let sock = term.__socket;
            sock.close();
            term.detach(sock);
        });
    } else {
    let url = (
        "ws://" + location.hostname + (location.port ? ':' + location.port : '') + "/sio1"),
        // "ws://localhost:8080/sio1"),
        sock = new WebSocket(url);

    sock.addEventListener('open', function () {
        term.attach(sock);
        ttyOnline.setAttribute('style', 'color: #0f0;');
    });
    sock.addEventListener('close', function () {
        term.detach(sock);
        ttyOnline.setAttribute('style', 'color: #f00;');
    });
    }
}

export default term;