import html from "./cpa-window.html"
import "./imsai-cpa.css"

export {
  load as load,
  connectCPA as connect,
  toggle as toggle,
  cpa_attached as attached,
  cancelReload as cancel
}

let cpa_attached = true;

let CPA = {};

function load() {

  let elmt = document.createElement('div');

  elmt.id = 'CPA'
  elmt.innerHTML = html;

  windows.appendChild(elmt);

  cpaUpdateStatus('E####');
}

var countdown;

function cancelReload() { 
    clearInterval(countdown); 
}; 

function offline() {
    let timeout = 10;
    modalBody.innerHTML = `Reloading in ${timeout} seconds.`;

    Modal.confirm(`System offline. Reload?`, () => { 
        location.reload(true); 
    }, 'Reload', cancelReload);

    countdown = setInterval(() => {
    timeout--;
    modalBody.innerHTML = `Reloading in ${timeout} seconds.`;
    if(timeout == 0) {
        clearInterval(countdown);
        location.reload(true);
    }
    }, 1000);
}

function toggle(sw, dir) {
  let cent = document.getElementById(sw + 'C');
  let up = document.getElementById(sw + 'U');
  let down = document.getElementById(sw + 'D');
  let cc = '';
  switch (dir) {
    case 'up':
      cent.classList.remove('top-image');
      down.classList.remove('top-image');
      up.classList.add('top-image');
      cc = sw[0] + dir[0];
      break;
    case 'down':
      cent.classList.remove('top-image');
      up.classList.remove('top-image');
      down.classList.add('top-image');
      cc = sw[0] + dir[0];
      break;
    case 'centre':
      up.classList.remove('top-image');
      down.classList.remove('top-image');
      cent.classList.add('top-image');
      break;
  }
  if (CPA.socket && cc) {
    CPA.socket.send(cc);
  }
}

function cpaUpdateStatus(data) {
  if (data[0] == 'U') {
    cpaToggle('pwr', 'up');
  } else if (data[0] == 'D') {
    cpaToggle('pwr', 'down');
  } else if (data[0] == 'E') {
    cpa_attached = false;
  }
  if (data[1] == 'I') {
    statusI.style.visibility = 'visible';
  } else {
    statusI.style.visibility = 'hidden';
  }
  if (data[2] == 'R') {
    statusR.style.visibility = 'visible';
  } else {
    statusR.style.visibility = 'hidden';
  }
  if (data[3] == 'W') {
    statusW.style.visibility = 'visible';
  } else {
    statusW.style.visibility = 'hidden';
  }
  if (data[4] == 'H') {
    statusH.style.visibility = 'visible';
  } else {
    statusH.style.visibility = 'hidden';
  }
}

function connectCPA() {
  if (CPA.socket) {
      Modal.confirm("Disconnect CPA from IMSAI 8080?", () => {
          let sock = CPA.socket;
          sock.close();
      });
  } else {
    let url = (
      "ws://" + location.hostname + (location.port ? ':' + location.port : '') + "/cpa"),
      sock = new WebSocket(url);

    sock.addEventListener('open', function () {
      CPA.socket = sock;
      cpaOnline.setAttribute('style', 'color: #0f0;');
      CPA.socket.send('P');
    });
    sock.addEventListener('close', function () {
      CPA.socket = null;
      cpaOnline.setAttribute('style', 'color: #f00;');
      offline();
    });
    sock.addEventListener('message', function (ev) {
      // console.log(ev.data);
      cpaUpdateStatus(ev.data);
    });
  }
}
