import * as props from 'codemirror/mode/properties/properties';

import 'codemirror/lib/codemirror.css';
import './vscode.css';

import './imsai-conf.css';
import html from "./cfg-window.html"

export {
    saveFile as saveFile,
    undoFile as undoFile,
    load as load
}

let myCodeMirror;
let myDoc = null;
let currDoc;
let docs = [];

function load() {

    let elmt = document.createElement('div');
  
    elmt.id = 'CFG'
    elmt.innerHTML = html;
  
    windows.appendChild(elmt);

    myCodeMirror = CodeMirror(editor, { lineNumbers: true, theme: 'vscode', viewportMargin: Infinity });
    getDir();
  }

