function saveFile() {
    if(docs[currDoc].doc && (docs[currDoc].doc.getValue() != docs[currDoc].text)) {
        // docs[currDoc].text = docs[currDoc].doc.getValue();
        // docs[currDoc].elmt.classList.remove('changedfile');
        sendDoc(currDoc);
    }         
}

function undoFile() {
    if(docs[currDoc].doc && (docs[currDoc].doc.getValue() != docs[currDoc].text)) {
        docs[currDoc].doc.setValue(docs[currDoc].text);
        docs[currDoc].elmt.classList.remove('changedfile');
    }         

}

function gotoDoc(docId) {

    currDoc = docId;

    // docs.forEach((e)=>{e.elmt.classList.remove('currentfile')});
    // docs[docId].elmt.classList.add('currentfile');

    if(docs[docId].text == null) {
        fetchDoc(docId);
    } else {
        // myCodeMirror.swapDoc(docs[docId].doc);
        // docs.forEach((e)=>{e.elmt.classList.remove('currentfile')});
        // docs[docId].elmt.classList.add('currentfile');   
        showDoc(docId);
    }
}

function showDoc(docId) {
    myCodeMirror.swapDoc(docs[docId].doc);
    docs.forEach((e)=>{e.elmt.classList.remove('currentfile')});
    docs[docId].elmt.classList.add('currentfile');   
}

function sendDoc(docId) {
    let xhttp = new XMLHttpRequest();

    xhttp.onreadystatechange = function () {
        if (this.readyState == XMLHttpRequest.DONE && this.status == 200) {
            // console.log(this.responseText);
            let res = JSON.parse(this.responseText);
            docs[docId].text = docs[docId].doc.getValue();
            docs[docId].elmt.classList.remove('changedfile');
            confSave.classList.remove('active');
            confUndo.classList.remove('active');
            Modal.confirm("File " + res.filename + " [" + res.size + " bytes] successfully saved.");
        }
    }
    xhttp.open("PUT", "/conf?"+ docs[docId].name, true);
    xhttp.send(new File([ docs[docId].doc.getValue() ], docs[docId].name, {type: 'text/plain'}));
}

function fetchDoc(docId) {
  let xhttp = new XMLHttpRequest();

  xhttp.onreadystatechange = function () {
    if (this.readyState == XMLHttpRequest.DONE && this.status == 200) {
        docs[docId].text = this.responseText;
        docs[docId].doc = CodeMirror.Doc(this.responseText, "properties");
        docs[docId].doc.on('change', () =>  {
            if(docs[docId].doc && (docs[docId].doc.getValue() != docs[docId].text)) {
                docs[docId].elmt.classList.add('changedfile');
                confSave.classList.add('active');
                confUndo.classList.add('active');
            } else {
                docs[docId].elmt.classList.remove('changedfile');
                confSave.classList.remove('active');
                confUndo.classList.remove('active');
            }
        });
        // myCodeMirror.swapDoc(docs[docId].doc);
        docs[docId].elmt.classList.add('loadedfile');
        showDoc(docId);
    }
  }
  xhttp.open("GET", "/imsai/conf/"+docs[docId].name+"?"+Date.now(), true);
  xhttp.send();
}

function getDir() {
  let xhttp = new XMLHttpRequest();
  let lst = [];
  let res;

  xhttp.onreadystatechange = function () {
    if (this.readyState == XMLHttpRequest.DONE && this.status == 200) {
      res = JSON.parse(this.responseText);
    //   console.log(res);

      lst = res.filter((a) => a.endsWith('.conf'));

    //   console.log(lst);
      filelist.innerHTML = '';  
      for(let i in lst) {

        let el = document.createElement('div');
        el.classList.add('file');
        el.innerHTML = lst[i];
        el.addEventListener('click', () => gotoDoc(i));

        docs.push({ name: lst[i] , doc: null, text:null, elmt: el });

        filelist.appendChild(el);
      }
    }
  }
  xhttp.open("GET", "/conf", true);
  xhttp.send();
}
