import * as Window from "../console/window";
import * as Modal from "../console/modal";
import {dsk} from "../lib/diskmanager";

import "./sys.css";
import html from "./sys-window.html";

export {
    load as load,
    openSYS as open,
    showSYS as show,
    reboot as reboot,
    sendReboot as sendReboot
}

function load() {
    let elmt = document.createElement('div');
  
    elmt.id = 'SYS'
    elmt.innerHTML = html;
  
    windows.appendChild(elmt);
}

function openSYS() {
    showSYS();
    Window.open(SYS);
}

function reboot() {
    modalBody.innerHTML = "Caution,this will stop everything.";

    Modal.confirm(`Reboot SYS: (Host) ?`, () => {
        sendReboot();
    }, 'Reboot');
}

function sendReboot(disk) {
    let xhttp = new XMLHttpRequest();

    xhttp.onreadystatechange = function () {
    if (this.readyState == XMLHttpRequest.DONE && this.status == 205) {

        Modal.confirm(`Reboot underway. Reload?`, () => {
            location.reload(true);
        }, 'Reload');

    }
    }
    xhttp.open("DELETE", "/system");
    xhttp.send();
}

function msToTime(duration) {
    var milliseconds = parseInt((duration%1000)/100)
        , seconds = parseInt((duration/1000)%60)
        , minutes = parseInt((duration/(1000*60))%60)
        , hours = parseInt((duration/(1000*60*60))%24);

    hours = (hours < 10) ? "0" + hours : hours;
    minutes = (minutes < 10) ? "0" + minutes : minutes;
    seconds = (seconds < 10) ? "0" + seconds : seconds;

    return hours + ":" + minutes + ":" + seconds + "." + milliseconds;
}

function showSYS() {
    let innerHTML = '';
    systemContainer.innerHTML = "";
    let xhttp = new XMLHttpRequest();

    xhttp.onreadystatechange = function () {

    let xhttp = new XMLHttpRequest();
    xhttp.onreadystatechange = function () {

        if (this.readyState == XMLHttpRequest.DONE && this.status == 200) {
        // console.log(this.responseText);
        let res = JSON.parse(this.responseText);
        res.tasks.sort( (a,b) => a.num - b.num);
        innerHTML = `<table><tr>
        <th>Num</th>
        <th>Process</th>
        <th>State</th>
        <th>Core</th>
        <th>Priority</th>
        <th>Stack</th>
        <th>Util</th>
        </tr>`;
        for(let t in res.tasks) {
            innerHTML += '<tr>'
            innerHTML += `<td>${res.tasks[t].num}</td>`;
            innerHTML += `<td>${res.tasks[t].name}</td>`;
            innerHTML += `<td>${res.tasks[t].state}</td>`;
            innerHTML += `<td>${res.tasks[t].core}</td>`;
            innerHTML += `<td>${res.tasks[t].pri}</td>`;
            innerHTML += `<td>${res.tasks[t].stack}</td>`;
            innerHTML += `<td>${res.tasks[t].pct?(res.tasks[t].pct+"%"):"<1%"}</td>`;
            innerHTML += '</tr>'
        }
        innerHTML += '</table></div>'
        systemContainer.innerHTML += innerHTML;
        }
    }

    if (this.readyState == XMLHttpRequest.DONE && this.status == 200) {
        // console.log(this.responseText);
        let system = JSON.parse(this.responseText);
        // console.log(system);
        if(system.about) {
        innerHTML += '<h2>About</h2><div>';
        innerHTML += `<p>${system.about.USR_COM}</p>`;
        innerHTML += `<p>${system.about.USR_REL}</p>`;
        innerHTML += `<p>${system.about.USR_CPR}</p>`;
        innerHTML += `<p>CPU: ${system.about.cpu} running at ${system.about.clock == 0 ? "unlimited" : (system.about.clock + "Mhz")}</p>`;
        innerHTML += `<p>Boot ROM: ${system.about.bootrom}</p>`;
        innerHTML += `<p>${system.about.cpa?("CP-A attached ["+system.about.cpa+"]"):"No CP-A attached"}</p>`;
        innerHTML += '</div>';          
        }
        if(system.system) {
        innerHTML += '<h2>Host System</h2><div>';
        innerHTML += `<p>Free Memory: ${Math.round(system.system.free_mem / 1024)}K</p>`;
        // innerHTML += `<p>Uptime: ${system.system.uptime} ms</p>`;
        innerHTML += `<p>Uptime: ${msToTime(system.system.uptime/1000)}</p>`;
        innerHTML += `<p>Time: ${new Date(system.system.time * 1000)}</p>`;
        innerHTML += '</div>';          
        }
        if(system.network) {
        innerHTML += '<h2>Network</h2><div>';
        innerHTML += `<p>IF: ${system.network.interface}</p>`;
        innerHTML += `<p>MAC: ${system.network.mac_address}</p>`;
        innerHTML += `<p>IP: ${system.network.ip_address}</p>`;
        innerHTML += `<p>Hostname: ${system.network.hostname}</p>`;
        innerHTML += '</div>';          
        }
        if(system.env) {
        innerHTML += '<h2>Environment</h2><div>';
        for (let env in system.env) {
            innerHTML += `<p>${env}=${system.env[env]}</p>`;
        }
        innerHTML += '</div>';          
        }
        if(system.paths) {
        innerHTML += '<h2>Paths</h2><div>';
        for (let path in system.paths) {
            innerHTML += `<p>${path}=${system.paths[path]}</p>`;
        }
        innerHTML += '</div>';          
        }
        innerHTML += '<h2>Disks</h2><div>';
        for (let d in dsk.disks()) {
            innerHTML += `<p>${d}:DSK: = "${dsk.disks()[d]}"</p>`;
        }
        innerHTML += '</div>';
        if(system.state) {
        innerHTML += '<h2>State</h2><div>';
        innerHTML += `<p>Last Error: ${system.state.last_error}</p>`;
        innerHTML += `<p>CPU Error: ${system.state.cpu_error}</p>`;
        innerHTML += `<p>Reset: ${system.state.reset}</p>`;
        innerHTML += `<p>Power: ${system.state.power}</p>`;
        innerHTML += '</div>';
        }
        if(system.partitions) {
        innerHTML += '<h2>Partitions</h2><div>';
        innerHTML += `<p>Running: ${system.partitions.run.label}</p>`;
        innerHTML += `<p>Boot: ${system.partitions.boot.label}</p>`;
        innerHTML += `<p>Next: ${system.partitions.next.label}</p>`;
        innerHTML += '</div>';
        }
        if(system.platform) {
        innerHTML += '<h2>Tasks</h2><div>';
        systemContainer.innerHTML += innerHTML;

        xhttp.open("GET", "/tasks", true);
        xhttp.send();
        } else {
        systemContainer.innerHTML += innerHTML;
        }
    }
    }
    xhttp.open("GET", "/system", true);
    xhttp.send();
}
