import * as Window from "../console/window";
import * as Modal from "../console/modal";

import "./cyc.css";
import html from "./cyc.html";

export {
    load as load,
    connect as connect
}

const constraints = {
    video: {width: {exact: 32}, height: {exact: 32}}
};

let video;
let cap;
let proc;
let showfps;
let bias;
let running;

function load() {
    let elmt = document.createElement('div');
  
    elmt.id = 'CYC'
    elmt.innerHTML = html;
  
    windows.appendChild(elmt);

    video = document.getElementById('video');
    cap = document.getElementById('cap');
    proc = document.getElementById('proc');
    showfps = document.getElementById('showfps');
    bias = document.getElementById('bias');
    running = document.getElementById('running');
    
    let preview = document.getElementById('preview');

    preview.ondblclick = function(ev) {

        if (ACC.preview == undefined) { 
            ACC.preview = 1; 
        } else {
            ACC.preview = (ACC.preview + 1) % preview.childElementCount;
        }

        for (let e=0; e < preview.childElementCount; e++) {
            if (e == ACC.preview) {
                preview.children[e].hidden = false;
            } else {
                preview.children[e].hidden = true;
            }
        }
    }
}

function updateCanvas(canv) {
    let canvas = canv;

    canvas.width = video.videoWidth;
    canvas.height = video.videoHeight;
    canvas.getContext('2d').drawImage(video, 0, 0);
}

function process(dest, frm = 16) {

    updateCanvas(cap);

    let ctx = cap.getContext("2d", { alpha: false });
    let cbuf = ctx.getImageData(0, 0, cap.width, cap.height);
    let cbuf8 = new Uint8Array(cbuf.data.buffer);
    let frac = Math.floor(256 / (frm+1));
    
    for(let i = 0; i < 1024; i++) {
        // if(mono.checked) {
            let a = cbuf8[(i*4) + 0] + cbuf8[(i*4) + 1] + cbuf8[(i*4) + 2];
            a = Math.floor(a/3); 
            if(ACC.bias) {
                // a = a>>(light.value);
                // if(a>15) a = 15;
                a = a * 2; if (a > 255) a = 255;
                // a = Math.ceil(a/frac);
            } else {
                // a = Math.ceil(a/frac);
            }
            a = Math.trunc(a / frac);
            cbuf8[(i*4) + 0] = a*frac;
            cbuf8[(i*4) + 1] = a*frac;
            cbuf8[(i*4) + 2] = a*frac;
        // } else {
        //     let a = cbuf8[(i*4) + 0] + cbuf8[(i*4) + 1] + cbuf8[(i*4) + 2];
        //     a = Math.floor(a/3); 
        //     a = Math.floor(a >> 7);

        //     let r = Math.floor(cbuf8[(i*4) + 0] >> 7)
        //     let g = Math.floor(cbuf8[(i*4) + 1] >> 7)
        //     let b = Math.floor(cbuf8[(i*4) + 2] >> 7)

        //     cbuf8[(i*4) + 0] = r ? (a?255:127) : 0;
        //     cbuf8[(i*4) + 1] = g ? (a?255:127) : 0;
        //     cbuf8[(i*4) + 2] = b ? (a?255:127) : 0;
        // }
    }
    
    ctx.putImageData(cbuf, 0, 0);
    
    dest.src = cap.toDataURL('image/webp');
}

let show_splits = false;

function split(frm, int) {
    process(proc, frm);
    
    let ctx = cap.getContext("2d", { alpha: false });
    splits.innerHTML = '';
    let frac = Math.floor(256/(frm+1));
    
    for (let s=frm; s>0; s--) {
        let cbuf = ctx.getImageData(0, 0, cap.width, cap.height);
        let cbuf8 = new Uint8Array(cbuf.data.buffer);
        let outbuf8 = new Uint8Array(128);
        
        for (let i = 0; i < 1024; i++) {
            let a = cbuf8[(i*4) + 0]
            if (a >= (s * frac)) { 
                a = 255;
            } else {
                a = 0;
            }
            // cbuf8[(i*4) + 0] = a;
            // cbuf8[(i*4) + 1] = a;
            // cbuf8[(i*4) + 2] = a;

            outbuf8[i>>3] |= (a & ( 1 << (i % 8)));
        }
        
        if(ACC && ACC.socket) {
            ACC.socket.send(outbuf8);
        }
        
        if (show_splits) {
            const src = document.createElement('canvas');
            let sx = src.getContext("2d", { alpha: false });
            src.height = 32;
            src.width = 32;
            
            sx.putImageData(cbuf, 0, 0);
            
            const img = document.createElement('img');
            img.height = 32;
            img.width = 32;
            
            img.src = src.toDataURL('image/webp');
            img.className = 'split';
            splits.appendChild(img);
        }
    }   
}

let ACC = {};

function connect() {

    if (ACC.socket) {
        Modal.confirm("Disconnect ACC from IMSAI 8080?", () => {
            let sock = ACC.socket;
            sock.close();
        });    
        return;
    }

    let url = (
    "ws://" + location.hostname + (location.port ? ':' + location.port : '') + "/acc"),
    sock = new WebSocket(url);

    sock.addEventListener('open', function () {
        ACC.socket = sock;
        ACC.start = Date.now();
        cycOnline.setAttribute('style', 'color: #0f0;');

        navigator.mediaDevices.getUserMedia(constraints).
            then((stream) => {
                video.srcObject = stream;
                video.onloadedmetadata = function(e) {
                    video.play();
                };
            });
    });

    sock.addEventListener('close', function () {
        ACC.socket = null;
        cycOnline.setAttribute('style', 'color: #f00;');

        video.pause();

        let tracks = video.srcObject.getTracks();

        tracks.forEach((track) => { track.stop();});

        video.srcObject = null;
    });

    sock.addEventListener('message', function (ev) {

        ACC.fields = ev.data.charCodeAt(0) & 0x0f;
        ACC.interval = (ev.data.charCodeAt(0) & 0x30) >> 4;
        ACC.bias = (ev.data.charCodeAt(0) & 0x40) >> 6;

        ACC.running = true;
        if(ACC.timeout) { clearTimeout(ACC.timeout); };
        running.classList.add('running');
        
        if (ACC.bias) {
            bias.classList.add('running');
        } else {
            bias.classList.remove('running');
        }
        
        split(ACC.fields);
        
        let end = Date.now();
        let fps = 1000/(end - ACC.start);
        showfps.innerHTML = `${ACC.fields}/${ACC.interval}@${fps.toFixed(1)}`;
        ACC.start = end;
        
        ACC.running = false;
        
        ACC.timeout = setTimeout(() => {
            ACC.timeout = null;
            running.classList.remove('running');
        }, 500);
    });
}