
import './imsai-vio.css';
import html from "./vio-window.html";
import charset from './imsai-vio-charset.js';

const tx = {
    'ArrowUp': String.fromCharCode(5),
    'ArrowDown': String.fromCharCode(24),
    'ArrowLeft': String.fromCharCode(19),
    'ArrowRight': String.fromCharCode(4),
    'AltArrowUp': String.fromCharCode(23),
    'AltArrowDown': String.fromCharCode(26),
    'AltArrowLeft': String.fromCharCode(1),
    'AltArrowRight': String.fromCharCode(6),
    'AltShiftArrowUp': String.fromCharCode(18),
    'AltShiftArrowDown': String.fromCharCode(3),
    'Escape': String.fromCharCode(27),
    'Backspace': String.fromCharCode(0x7f),
    'Enter': String.fromCharCode(13),
    'Tab': String.fromCharCode(9)
}

const full = [
    [255, 255, 255, 255],
    [64, 255, 32, 255],
    [255, 200, 96, 255]
];
const dim = [
    [32, 32, 32, 255],
    [8, 32, 4, 255],
    [32, 32, 16, 255]
]

function keyBoard(ev) {
    let key = -1;
    let inKey = (ev.ctrlKey ? 'Ctrl' : '')
        + (ev.altKey ? 'Alt' : '')
        + (ev.metaKey ? 'Meta' : '')
        + (ev.shiftKey ? 'Shift' : '')
        + ev.key;
    // console.log(ev);
    // console.log(String.fromCharCode(ev.keyCode), ev.keyCode);

    // if((ev.keyCode >= 0x20 && ev.keyCode < 0x7f)
    //     || ev.keyCode == 13 || ev.keycode == 8) {
    //         let key = String.fromCharCode(ev.keyCode);
    //         if(ws) ws.send(key);
    //     }

    if (ev.altKey && ev.code == "Escape") {
        if (ev.type == "keydown") doToggleFullScreen();
        return;
    }
  
    if (ev.key.length == 1) {
        if (ev.ctrlKey && (ev.key.toUpperCase() >= 'A') && (ev.key.toUpperCase() <= 'Z')) {
            key = String.fromCharCode(ev.key.toUpperCase().charCodeAt() - '@'.charCodeAt());
        } else {
            key = ev.key;
        }
    } else if (tx[inKey]) {
        key = tx[inKey];
    }
    ev.preventDefault();
    // if(key != -1) console.log(key.charCodeAt());
    if (this.socket && (key != -1)) this.socket.send(key);
}

let isFullscreen = false;

function doToggleFullScreen() {

    if (isFullscreen) {
        crt.vio.classList.add('vioFullscreen');
        isFullscreen = false;
    } else {
        crt.vio.classList.remove('vioFullscreen');
        isFullscreen = true;
    }
  }

const charPixWidth = 7;
const charPixHeight = 10;

class Vio {

    constructor() {
        this.bufA = new Uint8ClampedArray(2048);
        this.bufB = new Uint8ClampedArray(2048);
        this.rows = 24;
        this.cols = 80;
        this.scale = 1;
        this.dirty;
        this.lock = false;
        this.scrollBuf = new Uint8ClampedArray(this.bufA.buffer, this.cols);

        this.mode = 0;
        this.modeLast = null;;
        this.vmode = null;
        this.res = null;
        this.inv = null;
        this.xscale = null;
        this.yscale = null;
        this.scan = 2;

        this.vio = document.createElement('canvas');
        this.vio.width = this.cols * charPixWidth;
        this.vio.height = this.rows * charPixHeight * this.scan;
        this.w = this.vio.width;

        this.ctx = this.vio.getContext("2d", { alpha: false });
        this.cbuf = this.ctx.getImageData(0, 0, this.vio.width, this.vio.height);
        this.cbuf32 = new Uint32Array(this.cbuf.data.buffer);

        this.textarea = document.createElement('input')
        this.textarea.classList.add('vio-textarea');
 
        this.crtColour = 0;
        this.fullColour = full[this.crtColour];
        this.dimColour = dim[this.crtColour];

        // bufA.fill(32, 0);
        this.bufB.fill(0, 0);

        this.pos = 0;
        this.len = this.rows * this.cols;
        this.tp = 0;
        this.tb = 0;
        this.t0 = 0;
        this.f = 0;
    }
    
    load() {

        let elmt = document.createElement('div');
  
        elmt.id = 'CRT'
        elmt.innerHTML = html;
      
        windows.appendChild(elmt);

        this.container = vioContainer;
        this.container.style.height = `${this.vio.height}px`;

        this.container.appendChild(this.vio);
        this.container.insertAdjacentElement('afterend', this.textarea);
        this.container.classList.add('vio');
        // container.style.transformOrigin = '0 0';
        // container.style.transform = `scale(${scale*1.1428}, ${scale*2})`;
        // console.log(this.ctx);
        setInterval(this.refresh.bind(this), 33);
        this.textarea.addEventListener('keydown', keyBoard.bind(this));
        this.textarea.addEventListener('blur', (ev) => {
            if (this.container.classList) this.container.classList.remove('focus');
        });

        elmt.addEventListener('click', (ev) => {
            this.focus();
            // ev.preventDefault();
        });

        vioDownload.addEventListener('click', (ev) => {
            vioDownload.href = this.vio.toDataURL();
            vioDownload.download = `vio(${(new Date()).toJSON()}).png`;
            // ev.preventDefault();
        });

        vioFullscreen.addEventListener('click', (ev) => {
            // if (this.vio.webkitRequestFullscreen /*&& !document.fullscreenElement*/) {
                // this.vio.webkitRequestFullscreen(Element.ALLOW_KEYBOARD_INPUT);
            // }
            doToggleFullScreen();
        });
    }

    focus() {
        this.textarea.focus();
        setTimeout(() => { 
            this.textarea.focus();
        }, 500);               
        //     console.log('Focus');
        if (!this.container.classList.contains('focus'))
            this.container.classList.add('focus');
    }

    attach(sock) {
        this.socket = sock;
        this.socket.binaryType = 'arraybuffer';

        this.socket.addEventListener('open', function () {
        });
        this.socket.addEventListener('close', function () {
        });
        this.socket.addEventListener('message', this.getMessage.bind(this));
    }

    detach(sock) {
        this.socket = null;
    }

    connect() {
        // cpaUpdateStatus('.....');
        if (this.socket) {
            Modal.confirm("Disconnect VIO from IMSAI 8080?", () => {
                let sock = this.socket;
                sock.close();
                this.detach(sock);
            });    
        } else {
        let url = (
            "ws://" + location.hostname + (location.port ? ':' + location.port : '') + "/vio"),
            // "ws://localhost:8080/vio"),
            sock = new WebSocket(url);
    
        sock.addEventListener('open', () => {
            this.attach(sock);
            vioOnline.setAttribute('style', 'color: #0f0;');
        });
        sock.addEventListener('close', () => {
            this.detach(sock);
            vioOnline.setAttribute('style', 'color: #f00;');
        });
        }
    }

    getMessage(ev) {

        // console.log(ev.data);
        let m = new DataView(ev.data);
        let d = new Uint8ClampedArray(ev.data, 4);
        let addr = m.getUint16(0, true);
        let len;

        if (addr == 0xf7ff) {
            this.mode = m.getUint16(2, true);
            // console.log(`Mode: ${addr} of ${mode}`);
        } else {
            addr -= 0xf000;
            len = m.getUint16(2, true);
            if (len != d.length) console.log('LENGTH mismatch');
            // console.log(`Recv: ${addr} for ${len}`);

            // for(let i=0; i<len; i++) bufA[i+addr] = m.getUint8(i+4);
            this.lock = true;
            this.tb += len;
            this.tp++;
            this.bufA.set(d, addr);
            this.lock = false;
        }
    }

    // fill() {
    //     for (let i = 0; i < this.rows * this.cols; i++) {
    //         this.bufA[i] = i % 256;
    //     }
    // }

    // mat() {
    //     let chr = Math.floor(Math.random() * 128);

    //     this.bufA[this.pos++] = chr;

    //     if (this.pos >= this.len) {
    //         this.scroll();
    //         this.pos -= 80;
    //     }
    //     // pos %= len;
    // }

    // goMat(timestamp) {
    //     for (let i = 0; i < 79; i++) this.mat();
    //     window.requestAnimationFrame(this.goMat.bind(this));
    // }

    // scroll() {
    //     // console.time('Scroll');
    //     this.bufA.set(this.scrollBuf);
    //     this.bufA.fill(32, (this.rows - 1) * this.cols);
    //     // console.timeEnd('Scroll');
    // }

    // setScale(s) {
    //     this.scale = s;
    //     this.container.style.transform = `scale(${scale}, ${scale})`;
    // }

    colour() {
        this.crtColour++;
        this.crtColour %= full.length;
        this.fullColour = full[this.crtColour];
        this.dimColour = dim[this.crtColour];
        let t = this.mode;
        this.mode = 0;
        setTimeout(() => { this.mode = t; }, 100);
    }

    drawPixel(x, y, r, g, b, a) {
        let ind = ((y * this.w) * this.scan) + x;
        let c = (a << 24) | (b << 16) | (g << 8) | r;

        this.cbuf32[ind] = c;
        if (this.res & 1) {
            this.cbuf32[ind + 1] = c;
        }
        if (this.res & 2) {
            this.cbuf32[ind + this.w * this.scan] = c;
        }
        if ((this.res & 3) == 3) {
            this.cbuf32[ind + this.w * this.scan + 1] = c;
        }
    }

    renderCharacter(c, i, j) {
        let cinv = (this.vmode == 3) ? 0 : (c >> 7);
        this.dirty = true;
        for (let x = charPixWidth - 1; x >= 0; x--) {
            for (let y = charPixHeight - 1; y >= 0; y--) {
                if (charset[c & ((this.vmode < 3) ? 0x7f : 0xff) | ((this.vmode == 1) << 7)][y][x] ^ (cinv ^ this.inv)) {
                    this.drawPixel(i + (x << this.xscale), j + (y << this.yscale), ...this.fullColour);
                } else {
                    this.drawPixel(i + (x << this.xscale), j + (y << this.yscale), ...this.dimColour);
                }
            }
        }
    }

    updateCanvas() {
        if (this.dirty) {
            this.dirty = false;
            this.ctx.putImageData(this.cbuf, 0, 0);
        }
    }

    // let f=0;
    // let tt=0;
    // let tb=0;
    // let t0, t1;

    refresh() {


        if (this.lock) return;
        this.f++;
        // console.time('R');

        this.vmode = (this.mode >> 2) & 3;
        this.res = this.mode & 3;
        this.inv = (this.mode & 16) ? 1 : 0;

        if (this.res & 1) {
            this.cols = 40;
            this.xscale = 1;
        } else {
            this.cols = 80;
            this.xscale = 0;
        }

        if (this.res & 2) {
            this.rows = 12;
            this.yscale = 1;
        } else {
            this.rows = 24;
            this.yscale = 0;
        }

        if (this.vmode == 0) {
            if (!this.dirty) {
                // console.log(this.ctx);
                this.ctx.fillStyle = "#000000";
                this.ctx.fillRect(0, 0, this.vio.width, this.vio.height);
                this.dirty = true;
            }
        } else {
            let sy = 0;
            for (let y = 0; y < this.rows; y++) {
                let sx = 0;

                for (let x = 0; x < this.cols; x++) {
                    let loc = y * this.cols + x;
                    let val = this.bufA[loc];

                    if (val != this.bufB[loc] | (this.mode != this.modeLast)) {
                        this.renderCharacter(val, sx, sy);
                        this.bufB[loc] = val;
                    }
                    sx += charPixWidth << this.xscale;
                }
                sy += charPixHeight << this.yscale;
            }

            this.updateCanvas();
        }

        let t1, tt;

        this.modeLast = this.mode;
        if (this.f >= 30) {
            t1 = performance.now();
            tt = (t1 - this.t0) / 1000;
            // console.log('Framerate = ' + this.f/tt, tt, this.tb/1024/tt, ' KB/s', this.tb/this.tp, ' B/pkt', this.tp, ' Pkts', this.tb, ' Bytes');
            this.f = 0;
            this.tb = 0;
            this.tp = 0;
            this.t0 = performance.now();
        }
    }
}

let crt = new Vio();

export default crt;