import './dazzler.css';
import html from "./dazzler-window.html";

const size = 512;
const colors = [];
const grays = [];
colors[0] = "#000000";
colors[1] = "#800000";
colors[2] = "#008000";
colors[3] = "#808000";
colors[4] = "#000080";
colors[5] = "#800080";
colors[6] = "#008080";
colors[7] = "#808080";
colors[8] = "#000000";
colors[9] = "#FF0000";
colors[10] = "#00FF00";
colors[11] = "#FFFF00";
colors[12] = "#0000FF";
colors[13] = "#FF00FF";
colors[14] = "#00FFFF";
colors[15] = "#FFFFFF";
grays[0] = "#000000";
grays[1] = "#101010";
grays[2] = "#202020";
grays[3] = "#303030";
grays[4] = "#404040";
grays[5] = "#505050";
grays[6] = "#606060";
grays[7] = "#707070";
grays[8] = "#808080";
grays[9] = "#909090";
grays[10] = "#A0A0A0";
grays[11] = "#B0B0B0";
grays[12] = "#C0C0C0";
grays[13] = "#D0D0D0";
grays[14] = "#E0E0E0";
grays[15] = "#FFFFFF";

const aspect = [ 0.875, 0.75, 1.0 ];

function keyBoard(ev) {
    if (ev.altKey && ev.code == "KeyF") {
        if (ev.type == "keydown") doToggleFullScreen();
        return;
    }  
}

let isFullscreen = false;

function doToggleFullScreen() {

    if (isFullscreen) {
        dazzler.dzlr.classList.add('dzlrFullscreen');
        isFullscreen = false;
    } else {
        dazzler.dzlr.classList.remove('dzlrFullscreen');
        isFullscreen = true;
    }
}

class Dazzler {

    constructor() {
        this.bufA = new Uint8ClampedArray(2048);
        this.bufB = new Uint8ClampedArray(2048);
        this.dirty;
        this.lock = false;
        this.format = 0;

        this.dzlr = document.createElement('canvas');
        this.dzlr.width = size;
        this.dzlr.height = size;
        this.w = this.dzlr.width;

        this.ctx = this.dzlr.getContext("2d", { alpha: false });
        this.cbuf = this.ctx.getImageData(0, 0, this.dzlr.width, this.dzlr.height);
        this.cbuf32 = new Uint32Array(this.cbuf.data.buffer);

        // bufA.fill(32, 0);
        this.bufB.fill(0, 0);

        this.pos = 0;
        this.tp = 0;
        this.tb = 0;
        this.t0 = 0;
        this.f = 0;
    }

    load() {

        let elmt = document.createElement('div');

        elmt.id = 'DZLR'
        elmt.innerHTML = html;
        
        windows.appendChild(elmt);
        
        this.container = dzlrContainer;
        
        this.container.style.setProperty('--aspect', aspect[0]);
        this.container.style.height = `${size * aspect[0]}px`;
    
        this.container.classList.add('dazzler');
        
        this.container.appendChild(this.dzlr);

        setInterval(this.refresh.bind(this), 33);

        // this.container.addEventListener('keydown', keyBoard.bind(this));

        this.container.addEventListener('blur', (ev) => {
            if (this.container.classList) this.container.classList.remove('focus');
        });

        elmt.addEventListener('click', (ev) => {
            this.focus();
            // ev.preventDefault();
        });

        dzlrDownload.addEventListener('click', (ev) => {
            dzlrDownload.href = this.dzlr.toDataURL();
            dzlrDownload.download = `dazzler(${(new Date()).toJSON()}).png`;
        });

        dzlrFlatten.addEventListener('click', (ev) => {

            aspect.push(aspect.shift());
            // console.log(aspect);

            this.container.style.setProperty('--aspect', aspect[0]);
            this.container.style.height = `${size * aspect[0]}px`;
        });

        dzlrFullscreen.addEventListener('click', (ev) => {
            if (this.container.webkitRequestFullscreen /*&& !document.fullscreenElement*/) {
                this.container.webkitRequestFullscreen(Element.ALLOW_KEYBOARD_INPUT);
            }
            // doToggleFullScreen();
        });
    }

    focus() {
        if (!this.container.classList.contains('focus'))
            this.container.classList.add('focus');
    }

    attach(sock) {
        this.socket = sock;
        this.socket.binaryType = 'arraybuffer';

        this.socket.addEventListener('open', function () {
        });
        this.socket.addEventListener('close', function () {
        });
        this.socket.addEventListener('message', this.getMessage.bind(this));
    }

    detach(sock) {
        this.socket = null;
        this.bufB.fill(0, 0);
    }

    connect() {
        if (this.socket) {
            Modal.confirm("Disconnect Dazzler from IMSAI 8080?", () => {
                let sock = this.socket;
                sock.close();
                this.detach(sock);
            });
        } else {
            let url = (
                "ws://" + location.hostname + (location.port ? ':' + location.port : '') + "/dazzler"),
                // "ws://localhost:8080/dazzler"),
                sock = new WebSocket(url);

            sock.addEventListener('open', () => {
                this.attach(sock);
                dzlrOnline.setAttribute('style', 'color: #0f0;');
            });

            sock.addEventListener('close', () => {
                this.detach(sock);
                dzlrOnline.setAttribute('style', 'color: #f00;');
            });
        }
    }

    getMessage(ev) {

        // console.log(ev.data);
        let m = new DataView(ev.data);
        let d = new Uint8ClampedArray(ev.data, 6);

        let format = m.getUint16(0, true);
        let addr = m.getUint16(2, true);
        let len = m.getUint16(4, true);

        // console.log(`Format: ${format} addr: ${addr} len: ${len}`);

        if(format == 0 && addr == 0xFFFF && len == 0) {
            this.bufA.fill(0, 0);
            this.bufB.fill(0, 0);
            this.ctx.fillStyle = colors[0];
            this.ctx.fillRect(0, 0, size, size);
            return;
        }

        // console.log(`Recv: ${addr} for ${len}`);
        if (len != d.length) console.log('LENGTH mismatch');

        this.lock = true;
        this.dirty = true;
        this.format = format;
        this.tb += len;
        this.tp++;
        this.bufA.set(d, addr);
        this.lock = false;
    }

    proc_hires(addr, x, y, psize) {

        let i = this.bufA[addr];

        // if ((i != this.bufB[addr]) || (this.format != this.formatBuf)) {
        //     this.bufB[addr] = i;
        //     this.formatBuf = this.format

            if (i & 1) this.ctx.fillRect(x * psize, y * psize, psize, psize);
            if (i & 2) this.ctx.fillRect((x + 1) * psize, y * psize, psize, psize);
            if (i & 4) this.ctx.fillRect(x * psize, (y + 1) * psize, psize, psize);
            if (i & 8) this.ctx.fillRect((x + 1) * psize, (y + 1) * psize, psize, psize);
            if (i & 16) this.ctx.fillRect((x + 2) * psize, y * psize, psize, psize);
            if (i & 32) this.ctx.fillRect((x + 3) * psize, y * psize, psize, psize);
            if (i & 64) this.ctx.fillRect((x + 2) * psize, (y + 1) * psize,  psize, psize);
            if (i & 128) this.ctx.fillRect((x + 3) * psize, (y + 1) * psize, psize, psize);
        // }
    }

    draw_hires() {

        let i = this.format & 0x0f;
        let x, y, psize;
        let addr = 0;

        if (this.format & 16)
            // this.color = colors[i];
            this.ctx.fillStyle = colors[i];
        else
            // this.color = grays[i];
            this.ctx.fillStyle = grays[i];

        if (this.format & 32) {	/* 2048 bytes memory */
            psize = size / 128;	/* size of one pixel for 128x128 */
            for (y = 0; y < 64; y += 2) {
                for (x = 0; x < 64; x += 4) {
                    this.proc_hires(addr++, x, y, psize);
                }
            }
            for (y = 0; y < 64; y += 2) {
                for (x = 64; x < 128; x +=4) {
                    this.proc_hires(addr++, x, y, psize);
                }
            }
            for (y = 64; y < 128; y += 2) {
                for (x = 0; x < 64; x += 4) {
                    this.proc_hires(addr++, x, y, psize);
                }
            }
            for (y = 64; y < 128; y += 2) {
                for (x = 64; x < 128; x += 4) {
                    this.proc_hires(addr++, x, y, psize);
                }
            }
        } else {		/* 512 bytes memory */
            psize = size / 64;	/* size of one pixel for 64x64 */
            for (y = 0; y < 64; y += 2) {
                for (x = 0; x < 64; x += 4) {
                    this.proc_hires(addr++, x, y, psize);
               }
            }
        }
    }

    proc_lowres(addr, m, n, psize) {
        let i;
        let x = m;
        let y = n;
        let d = this.bufA[addr];

        // if ((d != this.bufB[addr]) || (this.format != this.formatBuf)) {
        //     this.bufB[addr] = d;
        //     this.formatBuf = this.format

            i = d & 0x0f;
            if (this.format & 16) {
                this.ctx.fillStyle = colors[i];
            } else {
                this.ctx.fillStyle = grays[i];
            }
            this.ctx.fillRect(x * psize, y * psize, psize, psize);
            x++;
            i = (d & 0xf0) >> 4;
            if (this.format & 16) {
                this.ctx.fillStyle = colors[i];
            } else {
                this.ctx.fillStyle = grays[i];
            }
            this.ctx.fillRect(x * psize, y * psize, psize, psize);
            x++;
        // }
    }

    draw_lowres() {
        let psize, x, y, i;
        let addr = 0;

        /* get size of DMA memory and draw the pixels */
        if (this.format & 32) {	/* 2048 bytes memory */
            psize = size / 64;	/* size of one pixel for 64x64 */
            for (y = 0; y < 32; y++) {
                for (x = 0; x < 32; x+=2) {
                    this.proc_lowres(addr++, x, y, psize);
                }
            }
            for (y = 0; y < 32; y++) {
                for (x = 32; x < 64; x+=2) {
                    this.proc_lowres(addr++, x, y, psize);
                }
            }
            for (y = 32; y < 64; y++) {
                for (x = 0; x < 32; x+=2) {
                    this.proc_lowres(addr++, x, y, psize);
                }
            }
            for (y = 32; y < 64; y++) {
                for (x = 32; x < 64; x+=2) {
                    this.proc_lowres(addr++, x, y, psize);
                }
            }
        } else {		/* 512 bytes memory */
            psize = size / 32;	/* size of one pixel for 32x32 */
            for (y = 0; y < 32; y++) {
                for (x = 0; x < 32; x+=2) {
                    this.proc_lowres(addr++, x, y, psize);
                }
            }
        }
    }

    refresh() {

        if (!this.dirty || this.lock || !this.socket) return;
        this.f++;
        // console.time('R');

        // clear the canvas ready to redraw
        this.ctx.putImageData(this.cbuf, 0, 0);
    
        if (this.format & 64)
            this.draw_hires();
        else
            this.draw_lowres();

        this.dirty = false;

        let t1, tt;

        if (this.f >= 30) {
            t1 = performance.now();
            tt = (t1 - this.t0) / 1000;
            // console.log('Framerate = ' + this.f/tt, tt, this.tb/1024/tt, ' KB/s', this.tb/this.tp, ' B/pkt', this.tp, ' Pkts', this.tb, ' Bytes');
            this.f = 0;
            this.tb = 0;
            this.tp = 0;
            this.t0 = performance.now();
        }
    }
}

let dazzler = new Dazzler();
export default dazzler;
