import html from "./lib-window.html"
import img_floppy from "./images/floppy.png";
import img_open from "./images/openDrive.png";
import img_closed from "./images/closedDrive.png";


let disks = {},
library = {},
dragged;

function loadLIB() {

    let elmt = document.createElement('div');
  
    elmt.id = 'LIB'
    elmt.innerHTML = html;
  
    windows.appendChild(elmt);
 }

function deletePrompt(disk) {
    modalBody.innerHTML = "";

    Modal.confirm(`Delete LIB: = "${disk}" ?`, () => {
        deleteDisk(disk);
    }, 'Delete');
    
}

function ejectPrompt(disk) {

    if (disks[disk]) {
        modalBody.innerHTML = "";

        Modal.confirm(`Eject ${disk}:DSK: = "${disks[disk]}" ?`, () => {
            eject(disk);
        }, 'Eject');            
    }
}

//TODO: this is a kludge because I don't kow enough about inport/export
function disksGetter() {

    return disks;
}

function showDisks() {
    let disksEl = document.getElementById('disks');
    let dsks = document.createElement('div');
    
    for (let d in disks) {

        let div = document.createElement('div');
        let img = document.createElement('img');
        let span = document.createElement('span');
        let eject = document.createElement('i');

        div.classList.add('device');
        img.id = `${d}:DSK:`;
        img.src = `${disks[d] ? img_closed : img_open}` 
        img.alt = `Drive ${d}:`;
        img.classList.add('drive');
        img.title = `${disks[d] ? disks[d] : 'EMPTY'}`;

        img.addEventListener('dragstart',   startDrag);
        img.addEventListener('dragend', endDrag);
        img.addEventListener('dragover', allowDiskDrop);
        img.addEventListener('drop', dropDisk);
        img.draggable = disks[d];

        span.innerHTML = `${d}:DSK:`;

        eject.classList.add('fa');
        if(disks[d]) {
            eject.classList.add('fa-eject', 'fa-lg', 'eject');
            eject.addEventListener('click', () => { ejectPrompt(d);} );
        } else {
            eject.classList.add('fa-ban', 'fa-2x', 'nodisk');
        }

        div.appendChild(eject);
        div.appendChild(img);
        div.appendChild(span);
        dsks.appendChild(div);
    }
    disksEl.innerHTML = "";
    disksEl.appendChild(dsks);
}

function download(fname) {
    let a = document.createElement('A');
    a.href = '/imsai/disks/'+fname;
    a.download = fname;
    a.click()
}

function showLIB() {
    let lib = document.createElement('div');

    for (let l in library) {
        let used = Object.values(disks).includes(library[l]);
        let div = document.createElement('div');
        let img = document.createElement('img');
        let span = document.createElement('span');

        div.classList.add('lib');
        img.id = `${l}:LIB:`;
        img.src = img_floppy;
        img.classList.add('disk');
        if(used) img.classList.add('used');
        img.title = library[l];
        img.addEventListener('dragstart', startDrag);
        img.addEventListener('dragend', endDrag);
        img.draggable = !used;
        span.innerHTML = library[l];

        div.appendChild(img);
        div.appendChild(span);
        lib.appendChild(div);
    }

    libraryContainer.innerHTML = "";
    libraryContainer.appendChild(lib);
}

function eject(disk) {
    let xhttp = new XMLHttpRequest();

    xhttp.onreadystatechange = function () {
        if (this.readyState == XMLHttpRequest.DONE && this.status == 200) {
            disks = JSON.parse(this.responseText);
            showDisks();
            showLIB();
        }
    }
    xhttp.open("DELETE", "/disks" + "?" + disk + ":DSK:", true);
    xhttp.send();
}

function load(disk, image) {
    let xhttp = new XMLHttpRequest();

    xhttp.onreadystatechange = function () {
        if (this.readyState == XMLHttpRequest.DONE && this.status == 200) {
            disks = JSON.parse(this.responseText);
            showDisks();
            showLIB();
        }
    }
    xhttp.open("PUT", "/disks" + "?" + disk, true);
    xhttp.send(image);
}

function getDisks() {
    let xhttp = new XMLHttpRequest();

    xhttp.onreadystatechange = function () {
        if (this.readyState == XMLHttpRequest.DONE && this.status == 200) {
            disks = JSON.parse(this.responseText);
            showDisks();
            showLIB();
            setTimeout(() => {
                let resize = new Event('resize');
                window.dispatchEvent(resize);
            }, 50);
        
        }
    }
    xhttp.open("GET", "/disks" + "?" + (new Date()).getTime(), true);
    xhttp.send();
}

function getLibrary(disk) {
    let xhttp = new XMLHttpRequest();
    let lib = {};
    let l;

    xhttp.onreadystatechange = function () {
    if (this.readyState == XMLHttpRequest.DONE && this.status == 200) {
        lib = JSON.parse(this.responseText);
        library = [];
        for (l in lib) {
            if (l != 'END') library.push(lib[l]);
        }
        library = library.filter(file => file.endsWith('.dsk')).sort();
        showLIB();
    }
    }
    xhttp.open("GET", "/library" + "?" + (new Date()).getTime(), true);
    xhttp.send();
}

function deleteDisk(disk) {
    let xhttp = new XMLHttpRequest();

    xhttp.onreadystatechange = function () {
        if (this.readyState == XMLHttpRequest.DONE && this.status == 200) {
            // console.log(this.responseText);
            getLibrary();
        }
    }
    xhttp.open("DELETE", "/library", true);
    xhttp.send(disk);
}

function allowDiskDrop(ev) {
    if (dragged && dragged.classList[0] == 'disk') ev.preventDefault();
}
function allowDriveDrop(ev) {
    if (dragged && dragged.classList[0] == 'drive') ev.preventDefault();
}
function startDrag(ev) {
    dragged = ev.target;
    ev.target.style.opacity = 0.5;
    ev.dataTransfer.setData("info", ev.target.id.split(':')[0]);
}
function endDrag(ev) {
    ev.target.style.opacity = '';
    dragged = null;
}
function dropDrive(ev) {
    ev.preventDefault();
    var dev = ev.dataTransfer.getData("info");
    eject(dev);
}
function dropDisk(ev) {
    ev.preventDefault();
    var dev = ev.dataTransfer.getData("info");
    if (disks[ev.target.id]) ejectPrompt(ev.target.id);
    if (!disks[ev.target.id]) {
        load(ev.target.id, library[dev]);
    }
}
function binDisk(ev) {
    ev.preventDefault();
    var dev = ev.dataTransfer.getData("info");
    // console.log('DELETE: '+library[dev]);
    deletePrompt(library[dev]);
}

function dwnldDisk(ev) {
    ev.preventDefault();
    var dev = ev.dataTransfer.getData("info");
    download(library[dev]);
}

function sendFile(dev, file) {
    let xhttp = new XMLHttpRequest();
    let res;

    xhttp.onreadystatechange = function () {
    if (this.readyState == XMLHttpRequest.DONE && this.status == 200) {
        res = JSON.parse(this.responseText);
        Modal.confirm("File " + res.filename + " [" + res.size + " bytes] successfully uploaded.");

        if(dev=='library') {
           getLibrary();
        }
    }
    }
    xhttp.open("PUT", "/" + dev + "?" + file.name, true);
    xhttp.send(file);
}

function fileDrop(dev, ev) {
    ev.preventDefault();
    let dt = ev.dataTransfer;
    let file = dt.items[0].getAsFile();

    if(dev == 'flash') {
        if(!file.name.endsWith('.bin')) {
            Modal.confirm('Only try to flash a binary');
            return;
        }
    }

    Modal.confirm("Upload " + file.name + " [" + file.size + " bytes] to .../" + dev + " ?", () => {
        sendFile(dev, file);
    });    
}

function fileDragOver(dev, ev) {
    let dt = ev.dataTransfer;
    if (dt.items[0].kind == "file") ev.preventDefault();
}

function fileDragEnd(dev, ev) {
    ev.preventDefault();
}

const lib = {
    load: loadLIB,
    get: getLibrary,
    delete: deleteDisk,
    allowDiskDrop,
    binDisk,
    dwnldDisk,
    fileDrop,
    fileDragOver,
    fileDragEnd,
    allowDriveDrop,
    dropDrive
};

const dsk = {
    get: getDisks,
    eject: eject,
    disks: disksGetter
}

export {
    lib,
    dsk
}