import "./window.css";

export {
    focusWindow as focus,
    initWindow as init,
    openWindow as open
}

let windowStack = [];
const windowOffset = 50;

window.addEventListener("resize", resizeThrottler, false);

var resizeTimeout;
function resizeThrottler() {
// ignore resize events as long as an actualResizeHandler execution is in the queue
    if ( !resizeTimeout ) {
        resizeTimeout = setTimeout(function() {
        resizeTimeout = null;
        actualResizeHandler();
        }, 500);
    }
}

function actualResizeHandler() {
// handle the resize event
    let winArr = windows.children;

    // console.log('RESIZE', winArr);
    for (let w = 0; w < winArr.length; w++) {
        // console.log('RESIZE:', w, winArr[w].id);
        resetIcon(winArr[w]);
    }
}

function focusWindow(win) {
    let top = windowStack.splice(windowStack.indexOf(win), 1);
    windowStack.push(top[0]);
    for (let i = 0; i < windowStack.length; i++) {
        windowStack[i].style.zIndex = i + windowOffset;
    }
    // win.focus();
}

function initWindow(win) {
    windowStack.push(win);
    win.style.zIndex = windowStack.length - 1 + windowOffset;
    win.classList.add('window', 'open');
    win.addEventListener('click', (ev) => { 
        focusWindow(win);
        // ev.preventDefault();
    });
    dragWindow(win);
    openWindow(win);
}

function resetIcon(win) {
    let icon = document.getElementById(win.id + '-icon');
    let rectIcon = icon.getBoundingClientRect();
    // console.log(win.id, win.offsetLeft, win.offsetTop, win.offsetWidth, win.offsetHeight);
    // console.log(icon.id, rectIcon.left, rectIcon.top, rectIcon.width, rectIcon.height);
    win.style.setProperty('--icon-x', ((rectIcon.left + rectIcon.width / 2) - (win.offsetLeft + win.offsetWidth / 2)) + 'px');
    win.style.setProperty('--icon-y', ((rectIcon.top + rectIcon.height / 2) - (win.offsetTop + win.offsetHeight / 2)) + 'px');
}

function openWindow(win) {
    if (win.classList.contains('open')) resetIcon(win);
    win.classList.toggle('iconify');
    win.classList.toggle('open');
    if (win.classList.contains('open')) focusWindow(win);
};

function dragWindow(elmnt) {
    var pos1 = 0, pos2 = 0, pos3 = 0, pos4 = 0;
    if (document.getElementById(elmnt.id + "-title")) {
        /* if present, the header is where you move the DIV from:*/
        document.getElementById(elmnt.id + "-title").onmousedown = dragMouseDown;
    } else {
        /* otherwise, move the DIV from anywhere inside the DIV:*/
        elmnt.onmousedown = dragMouseDown;
    }

    function dragMouseDown(e) {
    elmnt.classList.remove('open');
    elmnt.classList.add('drag');
    e = e || window.event;
    // get the mouse cursor position at startup:
    pos3 = e.clientX;
    pos4 = e.clientY;
    document.onmouseup = closeDragElement;
    // call a function whenever the cursor moves:
    document.onmousemove = elementDrag;
    }

    function elementDrag(e) {
    e = e || window.event;
    // calculate the new cursor position:
    pos1 = pos3 - e.clientX;
    pos2 = pos4 - e.clientY;
    pos3 = e.clientX;
    pos4 = e.clientY;
    // set the element's new position:
    elmnt.style.top = (elmnt.offsetTop - pos2) + "px";
    elmnt.style.left = (elmnt.offsetLeft - pos1) + "px";
    }

    function closeDragElement() {
    /* stop moving when mouse button is released:*/
    document.onmouseup = null;
    document.onmousemove = null;
    elmnt.classList.add('open');
    elmnt.classList.remove('drag');
    }
}
